import { Button } from 'antd';
import React, { useRef } from 'react';
import { Resolution, usePDF } from 'react-to-pdf';
import { useReactToPrint } from 'react-to-print';
import { PrintPage } from '../PrintPage';
import { PrintPagePdf } from '../PrintPagePdf';

export default function PrinterTestPage() {
	let printRef = useRef(null);

	let { toPDF, targetRef } = usePDF({
		filename: 'Print_Bill_Test.pdf',
		method: 'build',
		resolution: Resolution.EXTREME,
		overrides: {
			pdf: {
				compress: true
			}
		}
	});

	const handlePrint = useReactToPrint({
		content: () => printRef,
		onPrintError: (e) => console.log('Error Print!', e),
		removeAfterPrint: true,
		copyStyles: true
	});

	const handlePrintPdf = useReactToPrint({
		content: () => targetRef,
		onBeforeGetContent: () => {
			toPDF();
		},
		onPrintError: (e) => console.log('Error Print!', e),
		removeAfterPrint: true,
		copyStyles: true
	});

	return (
		<div className="w-full h-screen overflow-y-scroll">
			<div className="w-full h-full flex items-center justify-center">
				<div className="w-1/2 h-auto">
					<div className="w-full grid grid-cols-2 gap-3">
						<div>
							<div className="mb-3">
								<Button className="btn-page" onClick={handlePrint}>
									cetak struk
								</Button>
							</div>
							<PrintPage ref={(ref) => (printRef = ref)} isPrintTest={true}/>
						</div>
						<div>
							<div className="mb-3">
								<Button className="btn-page" onClick={handlePrintPdf}>
									cetak pdf
								</Button>
							</div>
							<PrintPagePdf ref={(ref) => (targetRef = ref)} />
						</div>
					</div>
				</div>
			</div>
		</div>
	);
}
